import React, { Component } from 'react';
import { portfolioData } from '../../data/portfolioData';
import Project from './Project';

class ProjectList extends Component {
    state = {
        // quand on appelle : this.state.projects , on recupère tous le tableau de portfolioData
        projects: portfolioData,
        radios: [
            { id: 1, languageName: "css" },
            { id: 2, languageName: "javascript" },
            { id: 3, languageName: "php" },
            { id: 4, languageName: "wordpress" },
            { id: 5, languageName: "swift" },
        ],
        selectedRadio: 'css',
    };

    handleRadio = (event) => {
        let radio = event.target.value;
        this.setState({ selectedRadio: radio })
    }

    render() {
        let { projects, radios, selectedRadio } = this.state;
        // distructuring => équivalent de this.state.projects 
        // ça évite de mettre des this.state à chaque fois
        // cf video : fetch data.json video => 2'45" 

        return (
            <div className="portfolioContent">
                <ul className="radioDisplay">
                    {
                        // on map les ≠ languages du tableau radios dans la data
                        radios.map((languagePerRadioType) => {
                            return (
                                <li key={languagePerRadioType.id}>
                                    <input
                                        type="radio"
                                        name="languagePerRadioType"
                                        checked={languagePerRadioType.languageName === selectedRadio}
                                        value={languagePerRadioType.languageName}
                                        id={languagePerRadioType.value}
                                        onChange={this.handleRadio}
                                    />
                                    <label htmlFor={languagePerRadioType.languageName}>{languagePerRadioType.languageName}</label>
                                </li>
                            )
                        })
                    }
                </ul>
                <div className="projects">
                    {
                        projects
                            // on trie + on sélectionne les cards qui contiennent le language puis on les map
                            .filter(itemAfterFilter => itemAfterFilter.languages.includes(selectedRadio))
                            .map(item => {
                                return (
                                    <Project
                                        key={item.id}
                                        item={item} />
                                )
                            })
                    }
                </div>
            </div>
        );
    }
}

export default ProjectList;