import React, { Component } from 'react';
import ProgressBar from './ProgressBar';

class Languages extends Component {
    state = {
        languages: [
            { id: 1, value: "Javascript", xp: 1 },
            { id: 2, value: "Css", xp: 2 },
            { id: 3, value: "Php", xp: .5 },
            { id: 5, value: "Wordpress", xp: 2},
        ],
        frameworks: [
            { id: 1, value: "React", xp: 1 },
            { id: 2, value: "Swift", xp: .5 },
            { id: 3, value: "Bootstrap", xp: 2 },
            { id: 4, value: "Sass", xp: 1.5 },
        ]
    }

    render() {
        let { languages, frameworks } = this.state;
        // équivalent de :
        //languages = this.state.languages & frameworks = this.state.frameworks
        // this : la fonction LANGUAGES
        // state : data de la fonction

        return (
            <div className="languagesFrameworks">
                <ProgressBar 
                    // props => on envoie dans le component "ProgressBar", la props "languages" qui est le tableau "languages"   
                    languages={languages}
                    className="languagesDisplay"
                    title="Languages"       
                />
                <ProgressBar 
                    languages={frameworks}
                    className="languagesDisplay"
                    title="Frameworks & Bibliothèques" 
                />
            </div>
        );
    }
}

export default Languages;