import React, { Component } from 'react';

class Quote extends Component {

    state = {
        quoteFromApi: '',
    };

    componentDidMount() {
        this.getQuoteRandom();
    }

    getQuoteRandom = () => {
        fetch('https://api.quotable.io/random')
            .then((res) => res.json())
            .then((data) => {
                this.setState({ quoteFromApi: data })
            })
    }

    render() {
        const { quoteFromApi } = this.state;

        return (
            <div className="quoteArea">
                <div className="quote">{quoteFromApi.content}<br/><br/>
                - {quoteFromApi.author} -</div>
                    <button onClick={this.getQuoteRandom}>Citation suivante<span className="fas fa-angle-double-right"></span></button>
            </div>
        );
    }
}

export default Quote;