import React, { Component } from 'react';

class Project extends Component {

    state = {
        // creation d'une popUp
        showInfo: false
        //   true: pop-up info s'affiche
        //  false: pop-up info disparait quand on clique
        // video : 3:00:00 -> 3:10:00
    }

    handleInfo = () => { // prise en chage de l'info
        this.setState({ // mise à jour du state avec setState
            showInfo: !this.state.showInfo
            // on donne l'inverse du resultat à showInfo, qui sera mis à jour dans le state
        })
    }

    render() {

        let { name, languagesIcons, picture, source, info } = this.props.item;
        // distrucuring ex.: name = this.props.item.name
        // video 2:54:34 : languageIcons.map

        return (
            <div className="project">
                <div className="icons">
                    {languagesIcons.map(languagesIconMapped =>
                        <i className={languagesIconMapped}
                            key={languagesIconMapped}>
                        </i>) // video 2:54:34 : languageIcons.map
                    }
                </div>

                <h3>{name}</h3>
                <img src={picture} alt="pic" onClick={this.handleInfo /*au clic, on applique handleInfo*/} />
                <span className="infos" onClick={this.handleInfo}>
                    <i className="fas fa-plus-circle"></i>
                </span>

                {
                    // Construction de la popUp
                    // si showInfo === true on affiche le JSX. ouvrir la popUpInfo, sinon la fermer
                    // &&if + JSX
                    this.state.showInfo && (
                        <div className="showInfos">
                            <div className="infosContent">
                                <div className="head">
                                    <h2>{name}</h2>
                                    <div className="sourceCode">
                                        <a href={source} rel="noopener noreferrer" className="button" target="_blank">Voir</a>
                                    </div>
                                </div>
                                <p className="text">{info}</p>
                                <div className="button return" onClick={this.handleInfo}>Fermer</div>
                            </div>
                        </div>
                    )
                }
            </div>
        );
    }
}

export default Project;